*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.footer, .subscription, .reviews, .project, .service, .clients, .hero, .header {
  margin-left: auto;
  margin-right: auto;
}

.subscription__title, .reviews__title, .project__title, .service__title, .clients__title, .hero__title {
  color: #000;
  margin: 0;
  font-size: 40px;
  font-weight: 700;
  line-height: 1.22;
}

.footer, .subscription, .reviews, .project, .service, .clients, .hero {
  margin-bottom: 40px;
  padding-left: 120px;
}

@keyframes pulse {
  0% {
    transform: translate(-50%, -50%)scale(1);
    box-shadow: 0 0 rgba(38, 58, 237, .475);
  }

  70% {
    transform: translate(-50%, -50%)scale(1.06);
    box-shadow: 0 0 0 15px rgba(238, 0, 111, 0);
  }

  100% {
    transform: translate(-50%, -50%)scale(1);
    box-shadow: 0 0 rgba(238, 0, 111, 0);
  }
}

@keyframes shine {
  0% {
    box-shadow: 0 0 rgba(38, 58, 237, .475);
  }

  70% {
    box-shadow: 0 0 0 15px rgba(238, 0, 111, 0);
  }

  100% {
    box-shadow: 0 0 rgba(238, 0, 111, 0);
  }
}

html {
  scroll-behavior: smooth;
}

body {
  max-width: 1440px;
  background-color: #fff;
  margin-left: auto;
  margin-right: auto;
  font-family: Raleway, sans-serif;
