  display: flex;
  position: absolute;
  bottom: 90px;
  right: 232px;
}

.slick-dots li:not(:last-child) {
  margin-right: 26px;
}

.slick-dots li button:before {
  width: 16px;
  height: 16px;
  content: "";
  background-color: #eff1ff;
  border-radius: 50%;
  font-family: slick;
  position: absolute;
}

.slick-dots li button:hover:before, .slick-dots li button:focus:before {
