.up {
  z-index: 200;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  animation: shine 2s infinite;
  display: flex;
  position: fixed;
  bottom: 40px;
  right: 40px;
  transform: rotate(270deg);
}

.up__img {
  margin: 0;
}

.backdrop {
  z-index: 20;
  visibility: visible;
  opacity: 1;
  width: 100vw;
  height: 100%;
  background-color: rgba(0, 0, 0, .25);
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1), transform .5s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1), transform .5s cubic-bezier(.4, 0, .2, 1), visibility .5s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1.2);
}

.contact-modal {
  width: 100%;
  max-width: 600px;
  background-color: #fff;
  border-radius: 30px;
  padding: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 10px 20px 50px rgba(0, 0, 0, .35);
}

.contact-modal:before {
  content: "";
  width: 130px;
  height: 130px;
  z-index: -1;
  background-color: #fff5db;
  border-radius: 50%;
  position: absolute;
  top: 142px;
  right: 170px;
}

.contact-modal:after {
  content: "";
  width: 175px;
  height: 154px;
  z-index: -1;
  background-image: url("dot-bg.3b456bc7.png");
  position: absolute;
  top: 122px;
  right: 40px;
}

.contact-modal__close-btn {
  cursor: pointer;
  width: 38px;
  height: 38px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.contact-modal__close-btn:hover, .contact-modal__close-btn:focus {
  transform: none;
}

.contact-modal__close-icon {
  stroke: #2639ed;
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
}

.contact-modal__close-btn:hover .contact-modal__close-icon, .contact-modal__close-btn:focus .contact-modal__close-icon {
  stroke: #ff007a;
}

.contact-modal-opened {
  touch-action: none;
  overflow: hidden;
